<template>
  <div class="box">
    <table class="My_table">
      <tr>
        <th>编号</th>
        <th>主题</th>
        <th>级别</th>
        <th>开始日期</th>
        <th>结束日期</th>
        <th>操作</th>
      </tr>
      <tr>
        <td>{{data.this_id}}</td>
        <td>{{data.title}}</td>
        <td>{{data.level}}</td>
        <td>{{(data.start_date || '').slice(0, 10)}}</td>
        <td>{{(data.end_date || '').slice(0, 10)}}</td>
        <td><a href="#"><i class="fa fa-pencil-square-o" aria-hidden="true"></i></a><span>&nbsp;||&nbsp;</span>
          <a href="#"><i class="fa fa-trash" aria-hidden="true"></i></a></td>
      </tr>
    </table >
    <h2>提交人信息</h2> <i class="add"></i>
    <table class="My_table">
      <tr>
        <th>姓名</th>
        <th>是否党员</th>
        <th>提交日期</th>
        <th>提交材料</th>
        <th>操作</th>
      </tr>
      <tr v-for="(item,index) in personal_data" :key="index">
        <td>{{ (item.apply_name).name}}</td>
        <td>{{(item.apply_name).party_member}}</td>
        <td>{{ (item.apply_date).slice(0, 10)}}</td>
        <td><img :src="'http://127.0.0.1:8000'+(item.materials).img_src" alt="" width="100px"></td>
        <td><a href="#"><i class="fa fa-pencil-square-o" aria-hidden="true"></i></a><span>&nbsp;||&nbsp;</span>
          <a href="#"><i class="fa fa-trash" aria-hidden="true"></i></a></td>

      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: "dangjianInfo",
  data (){
    return {
      id: this.$route.params.id,
      data: '',
      personal_data: '',
    }
  },
  methods: {
    getData(){
      const than = this
      than.axios.get('http://127.0.0.1:8000/dangjian/'+than.id+'?id='+than.id).then(res => {
        console.log(res.data);
        console.log(res.data.data);
        than.data = res.data
        than.personal_data = than.data.data
      })
    },
    getPersonal(){
      const than = this
      than.axios.get('http://127.0.0.1:8000/apply?apply_theme=1').then(
        res => {
          console.log(res.data);
        }
      )
    }
  },
  created (){
    this.getData()
    this.getPersonal()
  }
}
</script>

